.match {
    width: 22.5px;
    height: 75px;
    margin: 20px;
}

@media screen and (max-width: 780px) {
    .match { 
        width: 18px;
        height: 60px;
        margin: 10px;
    }
}

@media screen and (max-width: 590px) {
    .match { 
        width: 18px;
        height: 55px;
        margin: 7px;
    }
}
.match-modifier-btns {
  height: 30px;
  display: flex;
  justify-content: space-around;
}
.btn { 
  margin: 0 5px;
}
#match-original-game-container {
  width: 100%;
  padding: 50px 0;
}

.matches-original {
  display: flex;
  align-items: center;
}

.match-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin: 20px 0;
  height: 115px;
}

@media screen and (max-width: 780px) {
  .match-row { 
      height: 80px;
  }
}

@media screen and (max-width: 590px) {
  .match-row { 
      height: 70px;
      margin: 0;
  }
  #match-original-game-container {
    width: 100%;
    padding: 25px 0;
  }
}

.match-row:first-child {
  margin-top: 0px;
}

.match-row:last-child {
  margin-bottom: 0px;
}

.match-row:hover {
  background: rgba(255, 193, 7, 0.21);
}

.matches-fixed-width-container { 
  width: 550px;
  display: flex;
  justify-content: center;
}
#turn-action-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 900px;
    margin: 30px auto;
    display: flex;
    justify-content: space-evenly;
}

@media screen and (max-width: 590px) {
    #turn-action-bar {
        margin: 0 0 30px 0;
    }
}
#title-buttons {
    display: flex;
    justify-content: space-around;
    margin: 0 10vw;
}
.route { 
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    height: calc(100vh - 70px);
    width: 100vw;
}
.enter {
    opacity: 0;
}
.enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
}
.exit {
    opacity: 1;
}
.exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}
.navbar { 
    box-shadow: 0px 1px 6px -1px;
}
.App {
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Below are instance-specific modifications to the bootstrap defaults */
.jumbotron {
  background: #ffffff;
}

.text-center { 
  text-align: center;
}

body > * {
    touch-action: manipulation;  
}
